"use client";
import { Box, Typography, Avatar, Stack, Button, Tabs, Tab, Divider } from "@mui/material";
import { useState } from "react";

export default function ProfilePage() {
  const [tab, setTab] = useState(0);

  const handleTabChange = (_: any, newValue: number) => {
    setTab(newValue);
  };

  return (
    <Box
      sx={{
        background: "linear-gradient(to right, #b24592, #f15f79)",
        color: "white",
        p: 4,
        minHeight: "100vh",
      }}
    >
      {/* Profile Header */}
      <Stack direction="row" spacing={3} alignItems="center">
        <Avatar
          src="/user.jpg"
          alt="John Smith"
          sx={{ width: 80, height: 80, border: "3px solid white" }}
        />
        <Box>
          <Typography variant="h5" fontWeight={700}>
            John Smith
          </Typography>
          <Stack direction="row" spacing={2} alignItems="center">
            <Typography>üèÜ Level 12</Typography>
            <Typography>‚≠ê 1540 points</Typography>
          </Stack>
        </Box>
      </Stack>

      {/* Stats Row */}
      <Stack direction="row" spacing={4} mt={3}>
        <Typography>23 Completed</Typography>
        <Typography>5 Active</Typography>
        <Typography>8 Created</Typography>
      </Stack>

      {/* Tabs */}
      <Box mt={4} sx={{ backgroundColor: "#fff", borderRadius: 2, color: "#000", p: 2 }}>
        <Tabs value={tab} onChange={handleTabChange} centered>
          <Tab label="Friends" />
          <Tab label="Active" />
          <Tab label="Completed" />
          <Tab label="Created" />
        </Tabs>

        <Divider sx={{ my: 2 }} />

        {tab === 0 && (
          <Stack spacing={2}>
            <Typography variant="h6">Friends</Typography>
            <Box display="flex" alignItems="center" gap={2}>
              <Avatar>A</Avatar>
              <Box>
                <Typography fontWeight={600}>Anna Smirnova</Typography>
                <Typography variant="body2">
                  Level 15 ‚Ä¢ 2340 points ‚Ä¢{" "}
                  <span style={{ color: "green" }}>Online</span>
                </Typography>
              </Box>
              <Button variant="outlined">View</Button>
            </Box>
          </Stack>
        )}

        {tab === 1 && <Typography>Active challenges (coming soon)</Typography>}
        {tab === 2 && <Typography>Completed challenges (coming soon)</Typography>}
        {tab === 3 && <Typography>Created challenges (coming soon)</Typography>}
      </Box>
    </Box>
  );
}
